<template>
  <div id="app">
    <indicator></indicator>
        <head-bar>
          <a slot="logo" href="https://vision.udn.com/"><img class="other_logo" src="../static/visual-logo.png" alt="聯合報願景工程"></a>
          <a href="https://udn.com/upf/newmedia/udn_newmedia/">青貧向下流動</a>
          <a href="https://udn.com/upf/newmedia/udn_newmedia/">不公平的起跑點?</a>
          <a href="https://udn.com/upf/newmedia/udn_newmedia/">心靈貧窮了嗎?</a>
          <a href="https://udn.com/upf/newmedia/udn_newmedia/">怎麼幫他們</a>
          <a href="https://udn.com/upf/newmedia/udn_newmedia/">告別無望</a>
          <a href="https://udn.com/upf/newmedia/udn_newmedia/">迎向亮光</a>
        </head-bar>
        <cover name="添加副食品掌握3大關鍵" src="../static/CoverBg/bg01_m.jpg" src-web="../static/CoverBg/web/bg01.jpg" use-arrow="yes" use-animate="yes" use-hint="yes">
          <h1>添加副食品掌握3大關鍵 讓孩子身心成長更健康</h1>
        </cover>
        <content-container>
          <Share href="https://udn.com/upf/newmedia/2017_data/ideal/index.html"></Share>
          <p><b>「孩子3歲前要有自己專屬的副食品」</b>國內小兒科陳木榮醫師說。每位父母都知道「嬰幼兒的早期營養，3歲以前是關鍵」，除了當下的均衡之外，也是塑造孩子食物偏好和長大後飲食習慣的重要時期。
</p>
          <p><br></p>
          <p>但是，究竟該如何為孩子添加副食品才能面面兼顧？聯合報集合國內小兒科醫師、臨床營養師與職能治療師的觀點，透過不同的切入方式，談添加副食品的原則。讓爸媽們能「更新」自己的觀念，讓孩子在這段時期吃得健康、快樂長大！</p>
          <p><br></p>
        </content-container>
        <anchor title="觀念1">
          <parallax-bg 
          src="../static/CoverBg/b01_m.jpg" src-web="../static/CoverBg/web/b01.jpg">
            <h2 style="color: #fff">觀念1：把握3歲前的關鍵期　讓孩子養成好的飲食習慣</h2>
          </parallax-bg>
        </anchor>
        <content-container>
          <p>嬰兒期正是孩子成長最快速的階段，母乳或配方奶及副食品的添加成為主要的營養來源。根據衛福部今年公佈的《幼兒營養參考手冊》指出，<b>三歲前的營養攝取對於孩子日後的成長發展深具影響力</b>，除了先天遺傳的差異外，從哺餵方式到均衡的營養攝取，都是新手爸媽的重要課題。
</p>
          <p><br></p>
          <p>近年國內父母多遵照世界衛生組織的建議，以純母乳哺育至6 個月後為目標，再開始添加副食品。然而，臺灣嬰兒於4 個月後鐵質缺乏狀況之比例明顯上升。為降低缺鐵性貧血的機率，臺灣兒科醫學會則建議嬰兒滿4-6個月後，可以開始給予副食品，補足母奶以外的營養。
</p>
          <p><br></p>
          <Column-One description="0-6個月嬰兒主要營養素平均攝取不足百分比(%)
" src="./static/SectionBg/chart001_m.png" src-web="./static/SectionBg/web/chart001.png"></Column-One>
          <p><br></p>
          <p><b>飲食中之鐵和鋅多為生物利用率較低的食物，造成鐵和鋅之吸收受到抑制，故鐵缺乏通常易伴隨鋅缺乏
            資料來源：衛生福利部國民健康署《嬰兒期營養參考手冊》
            </b></p>
          
        </content-container>
  </div>
</template>

<script>
import Indicator from './components/Indicator'
import HeadBar from './components/HeadBar'
import Cover from './components/Cover'
import Content from './components/Content'
import Share from './components/Share'
import Anchor from './components/Anchor'
import ParallaxBg from './components/ParallaxBg'
import ColumnOne from './components/ColumnOne'

export default {
  name: 'App',
  components: {
    'indicator': Indicator,
    'head-bar': HeadBar,
    'cover': Cover,
    'content-container': Content,
    'Share': Share,
    'anchor': Anchor,
    'parallax-bg': ParallaxBg,
    'Column-One': ColumnOne
  }
}
</script>

<style>
#app {
  h1 {
    font-weight: 700;
    letter-spacing: -1px;
    line-height: 1.1;
    text-shadow: 0 0 18px #303030;
    margin: 0;
  }
}
</style>



